import React from 'react'
import { replace, length } from 'ramda'
import { Row, Col, Form, Tooltip } from 'antd'
import { ColumnProps } from 'antd/lib/table'
import { EyeOutlined, EditOutlined } from '@ant-design/icons'
import useTitle from '@/hooks/useTitle'
import { useBreadcrumb } from '@/hooks/useApp'
import { useReports } from '@/services/report'
import { DoctorSelect } from '@/services/pathology'
import Select from '@/components/select'
import Input from '@/components/input'
import { DatePicker } from '@/components/datepicker'
import { Page } from '@/components/layout'
import Table from '@/components/query-table'
import Button from '@/components/button'
import history from '@/utils/history'
import { REPORT_EDIT } from '@/constants/routes'
import { createFormItemLayout } from '@/utils/form'
import env from '@/constants/env'

const formItemLayout = createFormItemLayout(7)

export default function ReportList() {
  useTitle('清單查詢')

  const setBreadcrumb = useBreadcrumb([{ name: '清單查詢' }])

  const [form] = Form.useForm()

  const { data, refetch } = useReports()

  const handleOk = () => {
    refetch()
    setBreadcrumb([{ name: '清單查詢' }, { name: '查詢結果' }])
  }

  const handleToReport = (evt: React.MouseEvent<HTMLElement>) => {
    history.push(replace(':id', `${evt.currentTarget.dataset.id}`)(REPORT_EDIT))
  }

  const handleToImageViewer = (evt: React.MouseEvent<HTMLElement>) => {
    const url = replace(':id', '')(env.viewerUrl)
    window.open(`${url}?id=${evt.currentTarget.dataset.id}`, '__blank')
  }

  // const handleToMedicalOrder = (evt: React.MouseEvent<HTMLElement>) => {
  //   history.push(
  //     replace(':id', `${evt.currentTarget.dataset.id}`)(MEDICAL_ORDER)
  //   )
  // }

  const handleResetFields = () => form.resetFields()

  const columns: ColumnProps<any>[] = [
    {
      title: '編號',
      dataIndex: 'id',
      render: (_, __, idx) => idx + 1,
    },
    {
      title: '報告',
      dataIndex: 'x',
      render: (v, r) => (
        <Tooltip title="編輯、查看報告內容">
          <EditOutlined
            data-id={r.id}
            className="cursor-pointer text-lg"
            onClick={handleToReport}
          />
        </Tooltip>
      ),
    },
    {
      title: '影像',
      dataIndex: 'createAt',
      render: (v, r) => (
        <Tooltip title="開啟新視窗查看影像">
          <EyeOutlined
            data-id={r.id}
            className="cursor-pointer text-lg"
            onClick={handleToImageViewer}
          />
        </Tooltip>
      ),
    },
    { title: '開單醫師', dataIndex: 'serialNo' },
    { title: '病患姓名', dataIndex: 'HNAMEC' },
    { title: '掛號別', dataIndex: 'ORWORKNO' },
    { title: '檢查部位', dataIndex: 'ORWORKNO' },
    { title: '病房', dataIndex: 'HNURSTA' },
    { title: '檢查日期', dataIndex: 'creditDate' },
    { title: '檢查時間', dataIndex: 'creditTime' },
    { title: '開單日期', dataIndex: 'creditTime' },
    { title: '狀態', dataIndex: 'ORSTATUS' },
    { title: '排班醫師', dataIndex: 'ORWORKNO' },
    { title: '排班類別', dataIndex: 'HHISNUM' },
    { title: '報告日期', dataIndex: 'HBED' },
    { title: '報告時間', dataIndex: 'HSEX' },
    { title: '簽收日期', dataIndex: 'ZRSREGORD' },
    { title: '入賬日期', dataIndex: 'createAt' },
    { title: '入賬時間', dataIndex: 'CSECT' },
    // {
    //   title: '醫囑',
    //   dataIndex: 'ORPROCED',
    //   render: (v, r) => (
    //     <Tooltip title="查看醫囑內容">
    //       <EyeOutlined
    //         data-id={r.id}
    //         className="cursor-pointer text-lg"
    //         onClick={handleToMedicalOrder}
    //       />
    //     </Tooltip>
    //   ),
    // },
  ]

  return (
    <Page style={{ minHeight: 'calc(100vh - 203px)' }}>
      <Row justify="center">
        <Col md={22} xs={24}>
          <Form
            form={form}
            layout="horizontal"
            hideRequiredMark={true}
            labelAlign="right"
            colon={false}>
            <Row gutter={12} justify="space-between">
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="病歷號" name="no">
                  <Input />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="開單醫生" name="doctor">
                  <DoctorSelect allowClear />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="開單日期" name="createAt">
                  <DatePicker className="w-full" />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="掛號別" name="createAt">
                  <Select className="w-full" />
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={12} justify="space-between">
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="申請序號" name="no">
                  <Input />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="報告醫生" name="doctor">
                  <DoctorSelect allowClear />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="報告日期" name="createAt">
                  <DatePicker className="w-full" />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="急照別" name="createAt">
                  <Select className="w-full" />
                </Form.Item>
              </Col>
            </Row>{' '}
            <Row gutter={12} justify="space-between">
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="病人姓名" name="no">
                  <Input />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item
                  {...formItemLayout}
                  label="報告排班醫師"
                  name="doctor">
                  <DoctorSelect allowClear />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="檢查日期" name="createAt">
                  <DatePicker className="w-full" />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="逾期" name="createAt">
                  <Select className="w-full" />
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={12} justify="space-between">
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="身分證號" name="no">
                  <Input />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="輸入人員" name="doctor">
                  <DoctorSelect allowClear />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="入賬日期" name="createAt">
                  <DatePicker className="w-full" />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="病房" name="createAt">
                  <Select className="w-full" />
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={12} justify="space-between">
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="檢查部位" name="no">
                  <Select />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="排班類別" name="doctor">
                  <Select allowClear />
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item {...formItemLayout} label="報告狀態" name="state">
                  <Select allowClear>
                    <Select.Option value={1}>未輸入</Select.Option>
                    <Select.Option value={2}>複審</Select.Option>
                    <Select.Option value={3}>暫存</Select.Option>
                    <Select.Option value={4}>不確定</Select.Option>
                    <Select.Option value={5}>正式</Select.Option>
                    <Select.Option value={6}>全部</Select.Option>
                  </Select>
                </Form.Item>
              </Col>
              <Col md={6} xs={24}>
                <Form.Item className="ml-4">
                  <Button
                    color="primary"
                    style={{ width: 96 }}
                    className="mr-3"
                    onClick={handleResetFields}>
                    重設
                  </Button>
                  <Button
                    color="primary"
                    style={{ width: 96 }}
                    onClick={handleOk}>
                    搜尋
                  </Button>
                </Form.Item>
              </Col>
            </Row>
          </Form>
        </Col>
      </Row>
      {length(data.content) > 0 && (
        <>
          <Row className="mb-2 mt-4" justify="center">
            <Col md={24} xs={24} className="text-sm">
              檢索結果共 <span className="text-primary">{data.total}</span> 筆
            </Col>
          </Row>
          <Row justify="center">
            <Col md={24} xs={24}>
              <Table rowKey="id" dataSource={data} columns={columns} />
            </Col>
          </Row>
        </>
      )}
    </Page>
  )
}
